import * as React from 'react';

import { Container, Nav, Navbar, Offcanvas } from 'react-bootstrap';

import './navigation.css'

const Navigation: React.FC = () => {
  return (
    <Navbar expand="lg" className="bg-body-tertiary mb-3">
      <Container fluid>
        <Navbar.Brand href="#home" className='fixed-width-nav-brand'>Love 2 Craft LLC</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Offcanvas id="basic-navbar-nav">
          <Offcanvas.Body>
            <Nav className="justify-content-end flex-grow-1 p-3">
              <Nav.Link className='fixed-width-nav-link' href="/">Home</Nav.Link>
              <Nav.Link className='fixed-width-nav-link' href="/custom-orders/">Custom Orders</Nav.Link>
              <Nav.Link className='fixed-width-nav-link' href="/contact-me/">Contact Me</Nav.Link>
              <Nav.Link className='fixed-width-nav-link' href="/about-me/">About Me</Nav.Link>
              <Nav.Link className='fixed-width-nav-link' href="/resin/">Resin</Nav.Link>
              <Nav.Link className='fixed-width-nav-link' href="/crochet/">Crochet</Nav.Link>
              <Nav.Link className='fixed-width-nav-link' href="#link">Cards</Nav.Link>
            </Nav>
          </Offcanvas.Body>
        </Navbar.Offcanvas>
      </Container>
    </Navbar>
  )
}

export default Navigation