import * as React from 'react';
import { Button } from 'react-bootstrap';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEtsy, faFacebookF, faInstagramSquare, faPinterestP } from '@fortawesome/free-brands-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';

import { Link } from 'gatsby';

import './footer.css';

const Footer: React.FC = () => {
    return (
        <footer className="text-black container-fluid">
            <div className="p-2">
                <div className="row mt-4">
                    <div className="col-6 col-lg-5 col-xl-4 col-xxl-2">
                        <h4>Jinny Park</h4>

                        <div className="row p-2">
                            <Link className='col-2' to='https://www.facebook.com/love2craftllc.2024/'>
                                <Button variant='dark' size='sm' className='rounded-circle'>
                                    <FontAwesomeIcon icon={faFacebookF} />
                                </Button>
                            </Link>
                            <Link className='col-2' to='https://www.pinterest.com/love2craftllc/'>
                                <Button variant='dark' size='sm' className='rounded-circle'>
                                    <FontAwesomeIcon icon={faPinterestP} />
                                </Button>
                            </Link>
                            <Link className='col-2' to='https://www.instagram.com/love2craftllc/'>
                                <Button variant='dark' size='sm' className='rounded-circle'>
                                    <FontAwesomeIcon icon={faInstagramSquare} />
                                </Button>
                            </Link>
                            <Link className='col-2' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
                                <Button variant='dark' size='sm' className='rounded-circle'>
                                    <FontAwesomeIcon icon={faEtsy} />
                                </Button>
                            </Link>
                        </div>
                    </div>

                    <div className="col-6 col-lg-7 col-xl-8 col-xxl-10">
                        <div className='footer-text'>If you have any inquiries, please feel free to reach out!</div>
                        <div className='footer-text'>
                            <span> <FontAwesomeIcon icon={faEnvelope} /></span>
                            <span>Email: <a href="mailto:love2craft@gmail.com"></a>love2craft@gmail.com</span>
                        </div>
                    </div>
                </div>
            </div>

            <div className="text-center p-3 footer-text" style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}>
                &copy; 2025 Copyright
            </div>
        </footer>
    )
}

export default Footer